import _ from 'lodash'
export default {
  namespaced: true,
  state: {
    /**  */
    title: '',
    /** logo */
    logo: '',
    /**  */
    home: '',
    /**  */
    toolbar: {
      // 
      fullscreen: true,
      // 
      skin: true,
      // 
      logout: true,
      // 
      userInfo: true
    },
    // 
    ButtonPermission: true,
    /**  */
    menuUniqueOpened: true,
    /// //////////////////////////////////////////////////////////
    /**   */
    /// //////////////////////////////////////////////////////////
    /**  */
    logout: null,
    /**  */
    updatePassword: null,
    /**  */
    getLoginInfo: null,
    /**  */
    modules: []
  },
  actions: {
    async init ({ commit, dispatch }, { system, router }) {
      commit('init', { system })
      // 
      await dispatch('app/page/load', null, { root: true })
    },
    /**
     * @description 
     */
    async logout ({ state, dispatch }) {
      // 
      await state.logout()

      // 
      dispatch('app/account/logout', null, { root: true })
    }
  },
  mutations: {
    init (state, { system }) {
      _.merge(state, system)
      state.logo = state.logoUrl
      if (!state.logo) {
        state.logo = './images/logo.png'
      }
    }
  }
}
