<template>
  <nm-dialog class="nm-list-dialog" v-bind="dialog" v-on="on" :visible.sync="visible_">
    <template v-slot:toolbar>
      <!---->
      <nm-button icon="refresh" @click="refresh"/>
    </template>
    <slot/>
  </nm-dialog>
</template>
<script>
import dialog from '../../mixins/components/dialog.js'
export default {
  name: 'ListDialog',
  mixins: [dialog],
  data () {
    return {
      on: {
        open: this.onOpen,
        opened: this.onOpened,
        close: this.onClose,
        closed: this.onClosed
      }
    }
  },
  props: {
    /**  */
    title: String,
    /**  */
    icon: String,
    /** Dialog  */
    width: {
      type: String,
      default: '60%'
    },
    /** Dialog  */
    height: {
      type: [Number, String],
      default: '80%'
    },
    /**  */
    fullscreen: {
      type: Boolean,
      default: true
    },
    /**  modal  Dialog */
    closeOnClickModal: {
      type: Boolean,
      default: false
    },
    /**  */
    draggable: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    dialog () {
      return {
        noFooter: true,
        noScrollbar: true,
        title: this.title,
        icon: this.icon,
        width: this.width,
        height: this.height,
        fullscreen: this.fullscreen,
        closeOnClickModal: this.closeOnClickModal
      }
    }
  },
  methods: {
    refresh () {
      this.$slots.default.map(slot => {
        if (slot.componentOptions.tag === 'nm-list') {
          slot.componentInstance.refresh()
        }
      })
    },
    onOpen () {
      this.$emit('open')
    },
    onOpened () {
      this.$emit('opened')
    },
    onClose () {
      this.$emit('close')
    },
    onClosed () {
      this.$emit('closed')
    }
  }
}
</script>
