<template>
  <nm-dialog ref="form" class="nm-upload-dialog" v-bind="dialog" :visible.sync="visible_" @close="onClose">
    <div class="nm-upload-dialog-top">
      <el-upload ref="upload" v-bind="upload">
        <div>
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            
            <em></em>
          </div>
        </div>
        <div v-if="tip" class="nm-upload-multiple-tip">
          <slot name="tip">
            <i class="el-alert__icon el-icon-warning"></i>
            {{tip}}
          </slot>
        </div>
      </el-upload>
    </div>
    <div class="nm-upload-dialog-bottom">
      <el-table :data="fileList" border size="mini" height="100%" style="width: 100%;">
        <el-table-column align="center" type="index" width="50"></el-table-column>
        <el-table-column align="center" prop="name" label=""></el-table-column>
        <el-table-column align="center" prop="size" label="" width="180"></el-table-column>
        <el-table-column align="center" prop="ext" label="" width="180"></el-table-column>
        <el-table-column align="center" label="">
          <template v-slot:default="{row}">
            <nm-icon class="status-success" v-if="row.status===1" name="select"/>
            <nm-icon class="status-error" v-else-if="row.status===2" name="close"/>
            <nm-button type="text" icon="delete" text="" @click="onRemove(row)"/>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <template v-slot:footer>
      <nm-button type="success" text="" @click="onSubmit"/>
    </template>
  </nm-dialog>
</template>
<script>
import { mapState } from 'vuex'
import dialog from '../../mixins/components/dialog.js'

export default {
  name: 'UploadDialog',
  mixins: [dialog],
  data () {
    return {
      fileList: [],
      loading: false
    }
  },
  props: {
    /**  */
    title: {
      type: String,
      default: ''
    },
    /**  */
    icon: {
      type: String,
      default: 'upload'
    },
    /**  */
    width: {
      type: String,
      default: '50%'
    },
    /**  */
    height: {
      type: String,
      default: '80%'
    },
    /**  */
    action: {
      type: String,
      required: true
    },
    /**  */
    data: Object,
    /**  */
    accept: String,
    /**  */
    tip: String,
    /**  */
    limit: Number,
    /**  */
    maxSize: String,
    /**  */
    drag: {
      type: Boolean,
      default: true
    },
    /**  */
    resetOnClose: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    ...mapState('module/admin/token', ['accessToken']),
    dialog () {
      return {
        title: this.title,
        icon: this.icon,
        width: this.width,
        height: this.height,
        loading: this.loading,
        noScrollbar: true
      }
    },
    upload () {
      return {
        action: this.action,
        headers: {
          Authorization: 'Bearer ' + this.accessToken
        },
        data: this.data,
        accept: this.accept,
        limit: this.limit,
        drag: this.drag,
        multiple: true,
        autoUpload: false,
        showFileList: false,
        beforeUpload: this.onBeforeUpload,
        onChange: this.onChange,
        onSuccess: this.onSuccess,
        onError: this.onError,
        onExceed: this.onExceed
      }
    },
    maxSizeBytes () {
      if (this.maxSize) {
        const max = this.maxSize.toLowerCase()
        if (max.endsWith('kb')) {
          return parseFloat(max.replace('kb', '')) * 1024
        } else if (max.endsWith('mb')) {
          return parseFloat(max.replace('mb', '')) * 1024 * 1024
        } else if (max.endsWith('gb')) {
          return parseFloat(max.replace('gb', '')) * 1024 * 1024 * 1024
        } else if (max.endsWith('tb')) {
          return parseFloat(max.replace('tb', '')) * 1024 * 1024 * 1024 * 1024
        } else if (max.endsWith('b')) {
          return parseFloat(max.replace('b', ''))
        } else {
          return parseFloat(max)
        }
      }
      return ''
    }
  },
  methods: {
    clearFiles () {
      this.$refs.upload.clearFiles()
    },
    onClose () {
      if (this.resetOnClose) {
        this.$nextTick(() => {
          this.clearFiles()
        })
      }
    },
    onSubmit () {
      // 
      if (this.limit && this.fileList.length > this.limit) {
        this.onExceed()
        return
      }
      if (this.maxSizeBytes) {
        for (let i = 0; i < this.fileList.length; i++) {
          const file = this.fileList[i].localFile
          if (file.size > this.maxSizeBytes) {
            this._error(`${file.name}${this.maxSize}`)
            return
          }
        }
      }
      this.$refs.upload.submit()
    },
    onChange (file, fileList) {
      if (this.fileList.length > 0 && fileList.length === this.fileList.length) { return }
      this.fileList = []
      fileList.map(m => {
        this.fileList.push({
          name: m.name,
          ext: this.getFileExt(m.name),
          size: this.getSizeUnit(m.size),
          status: 0,
          localFile: m
        })
      })
    },
    onBeforeUpload (file) {
      this.loading = true
    },
    onSuccess (response, file) {
      for (let i = 0; i < this.fileList.length; i++) {
        let $file = this.fileList[i]
        if ($file.localFile === file) {
          if (response.code === 1) {
            $file.status = 1
          } else {
            $file.status = 2
          }
          if (i === this.fileList.length - 1) {
            this.loading = false
            this.$emit('success')
          }
        }
      }
    },
    onError () {
      this._error('')
    },
    onRemove (file) {
      if (file.localFile) {
        this.$refs.upload.handleRemove(file.localFile)
      }
      let fileList = this.fileList
      fileList.splice(fileList.indexOf(file), 1)
    },
    /**  */
    onExceed () {
      this._warning(`${this.limit}`)
    },
    /**  */
    getFileExt (name) {
      let ext = ''
      const arr = name.split('.')
      if (arr.length > 1) {
        ext = arr[arr.length - 1]
      }
      return ext
    },
    /**  */
    getSizeUnit (size) {
      if (size === null || size === '') {
        return '0 B'
      }
      var unitArr = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
      var index = 0
      var srcsize = parseFloat(size)
      index = Math.floor(Math.log(srcsize) / Math.log(1024))
      var s = srcsize / Math.pow(1024, index)
      s = s.toFixed(2)
      return s + unitArr[index]
    }
  }
}
</script>
