/**  */
export default {
  namespaced: true,
  state: {
    collapse: false
  },
  actions: {
    /**
     * @description 
     */
    toggle ({ state, commit }) {
      commit('collapseSet', !state.collapse)
    },
    /**
     * @description 
     */
    hide ({ commit }) {
      commit('collapseSet', false)
    },
    /**
     * @description 
     */
    show ({ commit }) {
      commit('collapseSet', true)
    }
  },
  mutations: {
    collapseSet (state, collapse) {
      state.collapse = collapse
    }
  }
}
