<template>
  <el-button
    class="nm-button"
    :type="type"
    :size="getSize"
    :plain="plain"
    :round="round"
    :circle="circle"
    :loading="loading"
    :disabled="disabled"
    :autofocus="autofocus"
    :native-type="nativeType"
    @click="remove"
  >
    <nm-icon v-if="!noIcon" name="delete"/>
    <span v-if="!circle&&text" class="nm-button-text" v-html="text"/>
  </el-button>
</template>
<script>
export default {
  name: 'ButtonDelete',
  props: {
    type: {
      type: String,
      default: 'text'
    },
    /**  */
    size: String,
    /**  */
    plain: Boolean,
    /**  */
    round: Boolean,
    /**  */
    circle: Boolean,
    /**  */
    loading: Boolean,
    /**  */
    disabled: Boolean,
    /**  */
    autofocus: Boolean,
    /**  type  button/submit/reset */
    nativeType: String,
    // 
    noIcon: Boolean,
    // 
    text: {
      type: String,
      default: ''
    },
    // 
    action: {
      type: Function,
      required: true
    },
    // id
    id: [Number, String, Object, Array],
    // 
    msg: String
  },
  computed: {
    getSize () {
      return this.size || this.fontSize
    }
  },
  methods: {
    async remove () {
      this._delete(async () => {
        await this.action(this.id)
        this.$emit('success')
      }, this.msg)
    }
  }
}
</script>
