import { db } from 'nm-lib-utils'
const TokenKey = 'token'

export default {
  /**
   * 
   */
  get() {
    return db.get(TokenKey)
  },
  /**
   * 
   * @param {String} token 
   */
  set(token) {
    db.set(TokenKey, token)
  },
  /**
   * 
   */
  remove() {
    db.set(TokenKey)
  }
}
