<template>
  <nm-form-dialog
    ref="form"
    title=""
    class="nm-update-password"
    width="400px"
    icon="password"
    :model="model"
    :rules="rules"
    :action="updatePassword"
    :visible.sync="visible_"
    success-msg-text=""
    @success="success"
  >
    <el-alert title="" type="warning" description="" show-icon :closable="false"></el-alert>
    <el-row :gutter="20">
      <el-col :span="18" :offset="2">
        <el-form-item label="" prop="oldPassword">
          <el-input type="password" v-model="model.oldPassword"></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPassword">
          <el-input type="password" v-model="model.newPassword"></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input type="password" v-model="model.confirmPassword"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
  </nm-form-dialog>
</template>
<script>
import dialog from '../../mixins/components/dialog.js'
import { mapState, mapActions } from 'vuex'

export default {
  name: 'UpdatePassword',
  mixins: [dialog],
  data () {
    // 
    const validator = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.model.newPassword) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      model: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      rules: {
        oldPassword: [
          { required: true, message: '' }
        ],
        newPassword: [
          { required: true, message: '' },
          { min: 6, message: '6' }
        ],
        confirmPassword: [{ validator }]
      }
    }
  },
  computed: {
    ...mapState('app/system', ['updatePassword'])
  },
  methods: {
    ...mapActions('app/system', ['logout']),
    success () {
      this.logout()
    }
  }
}
</script>
<style lang="scss" scoped>
.nm-update-password {
  .el-alert {
    margin-top: -5px;
    margin-bottom: 10px;
    line-height: 15px;
  }
  .el-visible__footer {
    line-height: 15px;
  }
}
</style>
