<template>
  <el-form
    class="nm-form"
    ref="form"
    :model="model"
    :rules="rules"
    :label-width="labelWidth"
    :size="fontSize"
    :inline="inline"
    :disabled="disabled"
    v-loading="showLoading"
    :element-loading-text="loadingText"
    :element-loading-background="loadingBackground"
    :element-loading-spinner="loadingSpinner"
  >
    <slot/>
  </el-form>
</template>
<script>
import loading from '../../mixins/components/loading'
export default {
  name: 'Form',
  mixins: [loading],
  data () {
    return {
      loading_: false
    }
  },
  props: {
    /**  */
    model: {
      type: Object,
      required: true
    },
    /**  */
    rules: Object,
    /**  */
    action: Function,
    /**  */
    inline: Boolean,
    /**  */
    successMsg: {
      type: Boolean,
      default: true
    },
    /**  */
    successMsgText: {
      type: String,
      default: ''
    },
    /**  */
    labelWidth: {
      type: String,
      default: '100px'
    },
    // 
    validate: Function,
    /**  */
    disabled: Boolean,
    /**  */
    loading: Boolean,
    /**  */
    noLoading: Boolean
  },
  computed: {
    showLoading () {
      return !this.noLoading && (this.loading_ || this.loading)
    }
  },
  methods: {
    /**  */
    submit () {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          // 
          if (!this.validate || this.validate() === true) {
            this.openLoading()

            this.action(this.model).then(data => {
              if (this.successMsg === true) {
                this._success(this.successMsgText)
              }

              this.$emit('success', data)

              this.closeLoading()
            }).catch(() => {
              this.$emit('error')
              this.closeLoading()
            })
          }
        } else {
          // 
          this.$emit('validate-error')
        }
      })
    },
    /**  */
    reset () {
      this.$refs.form.resetFields()
    },
    /**  */
    clearValidate (props) {
      this.$refs.form.clearValidate(props)
    },
    /**  */
    openLoading () {
      if (!this.noLoading) { this.loading_ = true }
    },
    /**  */
    closeLoading () {
      if (!this.noLoading) { this.loading_ = false }
    }
  }
}
</script>
