<template>
  <el-button
    class="nm-button"
    :type="type"
    :size="getSize"
    :plain="plain"
    :round="round"
    :circle="circle"
    :loading="loading"
    :disabled="disabled"
    :autofocus="autofocus"
    :native-type="nativeType"
    @click="remove"
  >
    <nm-icon v-if="!noIcon" name="batch-delete"/>
    <span v-if="!circle&&text" class="nm-button-text">{{text}}</span>
  </el-button>
</template>
<script>
export default {
  name: 'ButtonDeleteBatch',
  props: {
    type: {
      type: String,
      default: 'danger'
    },
    /**  */
    size: String,
    /**  */
    plain: Boolean,
    /**  */
    round: Boolean,
    /**  */
    circle: Boolean,
    /**  */
    loading: Boolean,
    /**  */
    disabled: Boolean,
    /**  */
    autofocus: Boolean,
    /**  type  button/submit/reset */
    nativeType: String,
    // 
    text: {
      type: String,
      default: ''
    },
    // 
    action: {
      type: Function,
      required: true
    },
    // 
    selection: Array,
    // 
    showProperty: String,
    // 
    noIcon: Boolean
  },
  computed: {
    getSize () {
      return this.size || this.fontSize
    }
  },
  methods: {
    async remove () {
      if (!this.selection || this.selection.length < 1) {
        this._error('~')
        return
      }

      let msg = ''
      if (this.showProperty) {
        msg += '<br/>' + this.selection.map(item => item[this.showProperty]).join('')
      }

      await this.$confirm(msg, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        dangerouslyUseHTMLString: true,
        type: 'warning'
      })

      // 
      if (this.action || typeof action === 'function') {
        await this.action(this.selection.map(item => item.id))
        this._success('~')
      }
    }
  }
}
</script>
