<template>
  <el-button
    class="nm-button"
    :type="type"
    :size="getSize"
    :plain="plain"
    :round="round"
    :circle="circle"
    :loading="loading"
    :disabled="disabled"
    :autofocus="autofocus"
    :native-type="nativeType"
    @click="$emit('click')"
  >
    <nm-icon v-if="!loading&&icon" :name="icon"/>
    <slot>
      <span v-if="!circle&&text" class="nm-button-text" v-html="text"/>
    </slot>
  </el-button>
</template>
<script>
export default {
  name: 'Button',
  props: {
    /**  */
    size: String,
    /**  primary/success/warning/danger/info/text */
    type: String,
    /**  */
    plain: Boolean,
    /**  */
    round: Boolean,
    /**  */
    circle: Boolean,
    /**  */
    loading: Boolean,
    /**  */
    disabled: Boolean,
    /**  */
    icon: String,
    /**  */
    autofocus: Boolean,
    /**  type  button/submit/reset */
    nativeType: String,
    // 
    text: String
  },
  computed: {
    getSize () {
      return this.size || this.fontSize
    }
  }
}
</script>
