<template>
  <nm-dialog ref="dialog" class="nm-form-dialog" v-bind="dialog" v-on="dialogOn" :visible.sync="visible_">
    <nm-form ref="form" v-bind="form" v-on="formOn">
      <slot/>
    </nm-form>

    <template v-slot:footer>
      <div class="nm-form-footer">
        <div class="nm-form-footer-left">
          <slot name="footer-left"/>
        </div>
        <div class="nm-form-footer-right">
          <slot name="fotter">
            <el-button v-if="btnOk" :type="btnOkType" @click="submit" :size="fontSize">{{btnOkText}}</el-button>
            <el-button v-if="btnReset" type="info" @click="reset" :size="fontSize"></el-button>
          </slot>
        </div>
      </div>
    </template>
  </nm-dialog>
</template>
<script>
import dialog from '../../mixins/components/dialog.js'
export default {
  name: 'FormDialog',
  mixins: [dialog],
  data () {
    return {
      loading_: false,
      formOn: {
        success: this.onSuccess,
        error: this.onError,
        'validate-error': this.onValidateError
      },
      dialogOn: {
        open: this.onOpen,
        opened: this.onOpened,
        close: this.onClose,
        closed: this.onClosed
      }
    }
  },
  props: {
    /**  */
    title: String,
    /**  */
    icon: String,
    /**  */
    width: String,
    /** Dialog  */
    height: [Number, String],
    /**  modal  Dialog */
    closeOnClickModal: {
      type: Boolean,
      default: false
    },
    /**  */
    fullscreen: Boolean,
    /**  */
    model: {
      type: Object,
      required: true
    },
    /**  */
    rules: Object,
    /**  */
    action: {
      type: Function,
      required: true
    },
    /**  */
    labelWidth: String,
    // 
    validate: Function,
    /**  */
    successMsg: {
      type: Boolean,
      default: true
    },
    /**  */
    successMsgText: {
      type: String,
      default: ''
    },
    /** Ok */
    btnOk: {
      type: Boolean,
      default: true
    },
    /** Ok */
    btnOkText: {
      type: String,
      default: ''
    },
    /** Ok */
    btnOkType: {
      type: String,
      default: 'primary'
    },
    /** reset */
    btnReset: {
      type: Boolean,
      default: true
    },
    /**  */
    customResetFunction: Function,
    // 
    closeWhenSuccess: {
      type: Boolean,
      default: true
    },
    /**  */
    disabled: Boolean,
    /**  */
    loading: Boolean,
    /**  */
    noLoading: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    dialog () {
      return {
        title: this.title,
        icon: this.icon,
        width: this.width,
        height: this.height,
        fullscreen: this.fullscreen,
        closeOnClickModal: this.closeOnClickModal,
        loading: this.showLoading
      }
    },
    form () {
      return {
        noLoading: true,
        model: this.model,
        rules: this.rules,
        action: this.action,
        labelWidth: this.labelWidth,
        validate: this.validate,
        successMsg: this.successMsg,
        successMsgText: this.successMsgText,
        disabled: this.disabled
      }
    },
    showLoading () {
      return !this.noLoading && (this.loading_ || this.loading)
    }
  },
  methods: {
    /**  */
    submit () {
      this.loading_ = true
      this.$refs.form.submit()
    },
    /**  */
    reset () {
      if (this.customResetFunction) {
        this.customResetFunction()
      } else {
        this.$refs.form.reset()
      }
    },
    /**  */
    clearValidate () {
      this.$refs.form.clearValidate()
    },
    /** loading */
    openLoading () {
      this.loading_ = true
    },
    /** loading */
    closeLoading () {
      this.loading = false
    },
    // 
    onSuccess (data) {
      // 
      if (this.closeWhenSuccess) {
        setTimeout(this.hide, 800)
      }
      this.loading_ = false
      this.$emit('success', data)
    },
    onError () {
      this.loading_ = false
      this.$emit('error')
    },
    onValidateError () {
      this.loading_ = false
      this.$emit('validate-error')
    },
    onOpen () {
      this.$emit('open')
    },
    onOpened () {
      this.$emit('opened')
    },
    onClose () {
      this.$emit('close')
    },
    onClosed () {
      this.$emit('closed')
    }
  }
}
</script>
