// 
const duration = 2500
export default {
  methods: {
    /**
     * @description 
     * @param {String} msg 
     */
    async _success (msg, onClose) {
      await this.$message({
        message: msg,
        showClose: true,
        type: 'success',
        duration,
        onClose
      })
    },
    /**
     * @description 
     * @param {String} msg 
     */
    async _warning (msg, onClose) {
      await this.$message({
        message: msg,
        showClose: true,
        type: 'warning',
        duration,
        onClose
      })
    },
    /**
     * @description 
     * @param {String} msg 
     */
    async _error (msg, onClose) {
      await this.$message({
        message: msg,
        showClose: true,
        type: 'error',
        duration,
        onClose
      })
    },
    /**  */
    async _confirm (msg, title, type) {
      await this.$confirm(msg, title || '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: type || 'warning'
      })
    },
    /**  */
    async _delete (action, title) {
      const msg = title || '?'

      await this._confirm(msg)

      // 
      if (action || typeof action === 'function') {
        await action()
        this._success('~')
      }
    }
  }
}
