<template>
  <nm-box class="nm-form-page" v-bind="box">
    <section class="nm-form-page-body">
      <nm-scrollbar :horizontal="false">
        <nm-form class="nm-form-page-main" ref="form" v-bind="form" v-on="formOn">
          <slot/>
        </nm-form>
      </nm-scrollbar>
    </section>

    <!---->
    <template v-slot:footer class="nm-form-page-footer">
      <div class="nm-form-page-footer-left">
        <slot name="footer-left"/>
      </div>
      <div class="nm-form-page-footer-right">
        <slot name="fotter">
          <el-button v-if="btnOk" :type="btnOkType" @click="submit" :size="fontSize">{{btnOkText}}</el-button>
          <el-button v-if="btnReset" type="info" @click="reset" :size="fontSize"></el-button>
        </slot>
      </div>
    </template>
  </nm-box>
</template>
<script>
export default {
  name: 'FormPage',
  data () {
    return {
      loading: false,
      formOn: {
        success: this.onSuccess,
        error: this.onError,
        'validate-error': this.onValidateError
      }
    }
  },
  props: {
    /**  */
    title: String,
    /**  */
    header: {
      type: Boolean,
      default: true
    },
    /** box */
    icon: {
      type: String,
      default: 'detail'
    },
    /**  */
    model: {
      type: Object,
      required: true
    },
    /**  */
    rules: Object,
    /**  */
    action: {
      type: Function,
      required: true
    },
    // 
    validate: Function,
    /**  */
    labelWidth: String,
    /**  */
    successMsg: {
      type: Boolean,
      default: true
    },
    /**  */
    successMsgText: {
      type: String,
      default: ''
    },
    /** Ok */
    btnOk: {
      type: Boolean,
      default: true
    },
    /** Ok */
    btnOkText: {
      type: String,
      default: ''
    },
    /** Ok */
    btnOkType: {
      type: String,
      default: 'primary'
    },
    /** reset */
    btnReset: {
      type: Boolean,
      default: true
    },
    /**  */
    fullscreen: {
      type: Boolean,
      default: true
    },
    /**  */
    disabled: Boolean
  },
  computed: {
    box () {
      return {
        page: true,
        title: this.title,
        icon: this.icon,
        header: this.header,
        footer: true,
        toolbar: this.toolbar,
        loading: this.loading
      }
    },
    form () {
      return {
        noLoading: true,
        model: this.model,
        rules: this.rules,
        action: this.action,
        labelWidth: this.labelWidth,
        validate: this.validate,
        successMsg: this.successMsg,
        successMsgText: this.successMsgText,
        disabled: this.disabled
      }
    },
    toolbar () {
      let toolbar = []
      if (this.fullscreen === true) {
        toolbar.push('fullscreen')
      }
      return toolbar
    }
  },
  methods: {
    /**  */
    submit () {
      this.loading = true
      this.$refs.form.submit()
    },
    /**  */
    reset () {
      this.$refs.form.reset()
    },
    /**  */
    clearValidate () {
      this.$refs.form.clearValidate()
    },
    /** loading */
    openLoading () {
      this.loading = true
      this.$refs.form.openLoading()
    },
    /** loading */
    closeLoading () {
      this.loading = false
      this.$refs.form.closeLoading()
    },
    // 
    onSuccess (data) {
      this.loading = false
      this.$emit('success', data)
    },
    onError () {
      this.loading = false
      this.$emit('error')
    },
    onValidateError () {
      this.loading = false
      this.$emit('validate-error')
    }
  }
}
</script>
