// 

const defaultOptions = {
  title: '',
  url: '',
  width: '60%',
  height: '70%',
  noScrollbar: true,
  fullscreen: true
}

export default {
  namespaced: true,
  state: {
    visible: false,
    title: '',
    url: '',
    width: '60%',
    height: '70%',
    noScrollbar: true,
    fullscreen: true,
    noFooter: true
  },
  actions: {
    /**
     * @description 
     */
    open ({ commit }, options) {
      commit('set', options)
    },
    /**
     * @description 
     */
    close ({ commit }, options) {
      commit('reset', options)
    }
  },
  mutations: {
    set (state, options) {
      Object.assign(state, options)
      state.visible = true
    },
    reset (state) {
      Object.assign(state, defaultOptions)
      state.visible = false
    }
  }
}
