import select from '../../mixins/components/select'
export default {
  name: 'Select',
  mixins: [select],
  props: {
    value: {
      type: [String, Number, Array],
      default: ''
    },
    // 
    multiple: Boolean,
    // 
    clearable: {
      type: Boolean,
      default: true
    },
    // 
    disabled: Boolean,
    // 
    method: Function
  },
  created () {
    this.action = this.method
  }
}
